

















import Vue from 'vue';
import Component from "vue-class-component";

@Component({})
export default class ImportMetadataFromXML extends Vue {
    xmlMetadata: string = "";
    errorMessage: string = "";
    isWorking: boolean = false;

    onSubmit() {
        this.isWorking = true;
        setTimeout(() => {
            this.errorMessage = "Import failed due to flash on Sun";
            this.isWorking = false;
        }, 1000)
    }
}
